    <v-main class="grey lighten-3">
      <v-container>
        <v-row>

          <v-col
              cols="12"
              sm="12"
          >
            <v-sheet
                min-height="70vh"
                rounded="lg"
            >
              <v-btn
                  elevation="2"
                  block
                  @click="generatePDF('hydrographprint')"
              >PDF</v-btn>
              <div id="hydrograph" />
              <div id="hydrographprint" style="display: none"/>

            </v-sheet>

            <v-sheet
                min-height="70vh"
                rounded="lg"
            >
              <v-btn
                  elevation="2"
                  block
                  @click="generatePDF('drillrodsgraphprint')"
              >PDF</v-btn>
              <div id="drillrodsgraph" />
              <div id="drillrodsgraphprint" style="display: none"/>
            </v-sheet>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
