<script>

import hydropgraph from '@d3-geology/hydrograph';
import drillrodsgraph from '@d3-geology/drillrodsgraph';
import * as d3 from 'd3';
import { jsPDF } from 'jspdf'
import 'svg2pdf.js'

export default {
  data: () => ({
    links: [
      'Charts'
    ],
  }),
  mounted() {
    this.generateCharts();
  },
  methods: {
    generatePDF(id) {
      const doc = new jsPDF()
      let element = document.getElementById(id ).firstChild;
      doc.svg(element, {
            x: 0,
            y: 0,
            width: 300,
            height: 600
          })
          .then(() => {
            // save the created pdf
            doc.save(id + '.pdf')
          })
    },
    generateCharts () {
      this.generateHydrograph();
      this.generateDrillrodsgraph();
    },
    generateHydrograph () {
      const w = 800;
      const h = 600;
      const margin = 50;

      const svg = d3
          .select("#hydrograph")
          .append("svg")
          .attr("width", w + (margin * 2))
          .attr("height", h + (margin * 2))
          .append('g')
          .attr('transform', 'translate(' + margin + ',' + margin + ')');

      hydropgraph(svg, dataHydro, w, h, margin);

      const svgPrint = d3
          .select("#hydrographprint")
          .append("svg")
          .attr("width", w + (margin * 2))
          .attr("height", h + (margin * 2))
          .append('g')
          .attr('transform', 'scale(0.20) translate(' + margin + ',' + margin + ')');

      hydropgraph(svgPrint, dataHydro, w, h, margin);
    },
    generateDrillrodsgraph () {
      const w = 800;
      const h = 800;
      const margin = 50;
      const svg = d3
          .select("#drillrodsgraph")
          .append("svg")
          .attr("width", w + (margin * 2))
          .attr("height", h + (margin * 2))
          .append('g')
          .attr('transform', 'translate(' + margin + ',' + margin + ')');

      drillrodsgraph(svg, dataDrillrods, w, h, margin);

      const svgPrint = d3
          .select("#drillrodsgraphprint")
          .append("svg")
          .attr("width", w + (margin * 2))
          .attr("height", h + (margin * 2))
          .append('g')
          .attr('transform', 'scale(0.25) translate(' + margin + ',' + margin + ')');

      drillrodsgraph(svgPrint, dataDrillrods, w, h, margin);
    }
  }

}

