import * as d3 from 'd3';
import * as d3c from 'd3-collection';
import Logger from 'js-logger';
import { findDin4023Def, pattern } from '@d3-geology/pattern'

const formatLocale = d3.formatLocale({decimal: ','});

export default function (svg, data, width, height, margin) {

  const context = svg;
  const extendsYValue = [];

  /*dataHydro.dataHydro.forEach((value) => {
    extendsYValue.push(...d3.extent(value.dataHydro, (d) => d.value));
  });*/

  const scaleStart = Math.ceil(data.start);
  const scaleHeight =  Math.ceil(scaleStart - data.start + parseFloat(d3.sum(data.levels, (d) => d.height)));
  const scaleEnd = scaleStart - scaleHeight;

  Logger.debug('scaleStart: ' + scaleStart);
  Logger.debug('scaleHeight: ' + scaleHeight);
  Logger.debug('scaleEnd: ' + scaleEnd);

  // Add Y axis
  const y = d3.scaleLinear()
    .domain([scaleStart, scaleEnd])
    .range([0, height]);

  let scaleTicks = buildScaleTicks(scaleHeight, 5);

  Logger.debug("Ticks: %O", scaleTicks)

  paintScale(context, scaleStart, scaleTicks, y, 5);

  let cLevelStart = data.start;
  let cHeight = 0;
  let cLabelPosRight = y(data.start);
  let cLabelPosLeft = y(data.start);

  const levels = data.levels.map((d) => {
    let levelStart = y(cLevelStart);
    let levelEnd = y((cLevelStart -= d.height).toFixed(2));
    cLabelPosRight = Math.max(cLabelPosRight + 50, levelEnd)
    cLabelPosLeft = Math.max(cLabelPosLeft + 25, levelEnd)
    return {
      ...d,
      levelStart: levelStart,
      levelEnd: levelEnd,
      labelPosRight: cLabelPosRight,
      labelPosLeft: cLabelPosLeft,
      labelHeight: cHeight += d.height
    };
  });

  Logger.debug("Levels: %O", levels);

  svg.selectAll('.levels')
    .data(levels)
    .enter().append('rect')
    .attr('x', (d) => 300)
    .attr('y', (d) => d.levelStart)
    .attr('fill', (d) => pattern()
      .size(100).texture(findDin4023Def(d.din4023)).render(svg).url())
    .attr('stroke', 'black')
    .attr('width', 200)
    .attr('height', (d) => d.levelEnd - d.levelStart);

  /**
   * Left legend
   */
  svg.selectAll('.labels')
    .data(levels)
    .enter().append('polyline')
    .attr('points', (d) => [[200, d.labelPosLeft], [250, d.labelPosLeft], [290, d.levelEnd], [300, d.levelEnd]].map((p) => p.join(',')).join(' '))
    .attr('fill', 'none')
    .attr('stroke', 'black');

  svg.selectAll('.labelsText')
    .data(levels)
    .enter().append('text')
    .attr('text-anchor', 'start')
    .attr('x', 200)
    .attr('y', (d) => d.labelPosLeft - 5)
    .text((d) => formatLocale.format('.2f')(d.labelHeight));

  /**
   * Right legend
   */
  svg.selectAll('.labels')
    .data(levels)
    .enter().append('polyline')
    .attr('points', (d) => [[700, d.labelPosRight], [550, d.labelPosRight], [510, d.levelEnd], [500, d.levelEnd]].map((p) => p.join(',')).join(' '))
    .attr('fill', 'none')
    .attr('stroke', 'black');

  svg.selectAll('.labelsTextHeight')
    .data(levels)
    .enter().append('text')
    .attr('text-anchor', 'start')
    .attr('x', 560)
    .attr('y', (d) => d.labelPosRight - 5)
    .text((d) => formatLocale.format('.2f')(d.height));

  svg.selectAll('.labelsTextDescription')
    .data(levels)
    .enter().append('text')
    .attr('text-anchor', 'start')
    .attr('x', 640)
    .attr('y', (d) => d.labelPosRight - 5)
    .text((d) => d.description);

  /**
   * Upper Legend
   */
  svg.append('text')
    .attr('text-anchor', 'start')
    .attr('x', 320)
    .attr('y', y(data.start) - 5)
    .text('NHN+' + formatLocale.format('.2f')(data.start) + 'm');

  svg.append('polyline')
    .attr('points', [[0, 0], [14, 0], [7, 10], [0, 0]].map((p) => p.join(',')).join(' '))
    .attr('fill', 'none')
    .attr('stroke', 'black')
    .attr("transform", "translate(300," + (y(data.start) - 10) + ")");

}

function buildScaleTicks (scaleHeight, diff) {
  let ticks = [];
  let scaleVal = 0;
  while (scaleVal < scaleHeight) {
    ticks.push(scaleVal);
    scaleVal += diff;
  }

  if (ticks.length > 0 && ticks[ticks.length - 1] !== scaleHeight) {
    ticks.push(scaleHeight);
  }

  return ticks;
}

function paintScale(context, start, ticks, scale) {
  const width = 10;
  const x = 10;
  const tickLength = 100;
  const textPadding = 5;
  context.selectAll('.rect')
    .data(d3c.keys(ticks).slice(0, -1))
    .enter().append('rect')
    .attr('fill', (d) => d % 2 === 0? 'red': 'white')
    .attr('x', x)
    .attr('y', (d) => scale(start - ticks[d]))
    .attr('stroke', 'black')
    .attr('width', width)
    .attr('height', (d) => scale(start - ticks[++d])  - scale(start - ticks[--d]) )

  context.selectAll('.tick')
    .data(ticks)
    .enter().append('line')
    .attr('x1', x)
    .attr('y1', (d) => scale(start - d))
    .attr('x2', tickLength)
    .attr('y2', (d) => scale(start - d))
    .attr('stroke', 'black')

context.selectAll('.ticktext')
  .data(ticks)
  .enter().append('text')
  .attr('text-anchor', 'end')
  .attr('x', tickLength)
  .attr('y', (d) => scale(start - d) - textPadding)
  .text((d) => formatLocale.format('.2f')(start - d));

}

